import Layout from "../components/MyLayout";
import fetch from "isomorphic-unfetch";
import { Component } from "react";

class Post extends Component {
  constructor(props) {
    super(props);
    this.state = {
      article: props.article
    };
  }
  render() {
    console.log(this.state.article.images[0].mainUrl);
    return (
      <Layout>
        <h1>{this.state.article.title}</h1>
        <div className="wrapper">
          <section className="main">
            <img src="http://abbeyjfitzgerald.com/wp-content/uploads/2017/06/flexbox-header.jpg" />
            <h2>Flexbox Layout</h2>

            <p>
              Starting from the outside and working in, adding display: flex; to
              the container is the first step in any flexbox layout. The
              flex-direction is column, so this will position all sections under
              each other.{" "}
            </p>

            <p>
              One handy thing is how easy it is to align text. In the
              navigation, with align-items: baseline, all navigation items are
              aligned to the baseline of the text so they look more uniform.{" "}
            </p>
          </section>
        </div>
        {/* <p>{props.show.summary.replace(/<[/]?p>/g, "")}</p> */}
        {/* <img src={`http://${this.state.article.images[0].mainUrl}`} /> */}
      </Layout>
    );
  }
}

Post.getInitialProps = async function(context) {
  const { id } = context.query;
  const res = await fetch(
    `https://interview-project-17987.herokuapp.com/api/article/article-${id}`
  );
  const article = await res.json();

  console.log(`Fetched show: ${article.url}`);

  return { article };
};

export default Post;
